.movies .movie {
    width: 45%;
    background-color: #fff;
    margin-bottom: 70px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    font-weight: 300;
    border-radius: 5px;
    padding: 20px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025); 

}
.movie__data {
    width: 75%;
}
.movie img {
    position: relative;
    top: -50px;
    width: 100%;
    max-width: 150px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 90, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3) 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie .movie__title, .movie .movie__year {
    font-weight: 300;
    margin: 0;
}
.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;

}
.movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0;
}
.movie__genres li {
    font-size: 14px;
    margin-right: 10px;
}